.employee-modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.4);
    display: flex;
    justify-content: center;
    overflow-y: scroll;
    flex-wrap: wrap;
    z-index: 2;
  }
  
  .employee-modal-content {
    display: flex;
    flex-direction: column;
    width: 600px;
    background: white;
    border-radius: 5px;
    border-radius: 30px;
  }

.employee-section-title{
    font-size: 22px;
    font-weight: 500;
    margin-top: -30px;
  }
  
  .schedule-title-wrapper {
    display: flex;
    justify-content: space-between;
    padding-bottom: 10px;
    border-bottom: 1px solid #000000;
  }
  
  .schedule-title {
    width: 43%;
    font-size: 18px;
    font-weight: 700;
  }
  
  .schedule-wrapper {
    padding-top: 10px;
  }
  
  .day-time-input {
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 20px;
    font-weight: 600;
    margin-bottom: 10px;
  }
  
  .schedule-days {
    width: 43%;
    display: flex;
    justify-content: space-between;
  }
  
  .schedule-hours {
    width: 43%;
    display: flex;
    justify-content: space-between;
  }
  
  .working-hours-checkbox {
    display: none;
  }
  
  .working-days {
    position: relative;
    width: 89%;
    cursor: pointer;
    display: inline-block;
    user-select: none; 
  }
  
  .working-days::before {
    content: "";
    position: absolute;
    top: 0;
    left: 100%;
    width: 22px;
    height: 22px;
    border: 1px solid #CCCCCC;
    border-radius: 4px;
    background-color: #F5F5F5;
  }
  
  .working-hours-checkbox:checked + .working-days::before {
    content: url("../../../images/check.svg");
    color: black;
    text-align: center;
    line-height: 25px;
    border: 1px solid #00315D;
  }
  
  .working-hours {
    font-family: "Nunito", sans-serif;
    font-size: 20px;
    width: 85px;
    height: 36px;
    text-align: center;
    background: none;
    border: 1px solid #C1C1C3;
    border-radius: 4px;
  }
  
  .working-hours:disabled {
      color: #C1C1C3;
  }
    
  